/* ==================================
	Common styles
/* =================================*/
img {
  user-select: none !important;
  pointer-events: none !important;
}

button {
  line-height: 100% !important;
}

:root {
  --ff-gilroy: "Gilroy", "sans-serif";
  --bg-def-dark: #0E0E0F;
  --bg-def-dark2: #1A1A1A;
  --bg-343434: #343434;
  --bg-F8F8F8: #F8F8F8;
}

.h93 {
  width: 100%;
  height: 93px;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  outline: none;
  text-decoration: none;
  transition: 0.3s all ease;
}

html {
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}

html,
body {
  height: 100%;
  scroll-behavior: smooth;
}

body {
  font-family: var(--ff-gilroy);
  leading-trim: both;
  text-edge: cap;
  font-weight: 400;
  letter-spacing: normal;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  background-color: var(--bg-def-dark);
}

a,
button {
  cursor: pointer;
}

.close-bg {
  position: absolute;
  width: 100%;
  height: 100%;
  display: none;
}

.wrapper {
  overflow: hidden;
}

.container {
  width: 100%;
  max-width: 1394px;
  margin: 0 auto;
}

.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  padding: 15px 30px;
  background-color: var(--bg-F8F8F8);
  border-radius: 8px;
  color: var(--bg-def-dark2);
  leading-trim: both;
  text-edge: cap;
  font-size: 17px;
  font-weight: 500;
  line-height: 100%;
  cursor: pointer;
}

.dark-light-btn {
  width: 44px;
  height: 44px;
  padding: 10px;
  background-color: var(--bg-343434);
}
.dark-light-btn svg {
  width: 100%;
  height: 100%;
}

.title-block {
  text-align: center;
}
.title-block .title {
  color: var(--bg-F8F8F8);
  font-size: 32px;
  font-weight: 600;
  line-height: 26px;
}
.title-block .title-txt {
  color: #B4B4B4;
  font-size: 20px;
  font-weight: 500;
  line-height: 26px;
  margin-top: 28px;
}

/*======================
	Header
=====================*/
header {
  padding-top: 20px;
  position: absolute;
  width: 100%;
  left: auto;
  right: auto;
}
header .header-wrapper {
  border-radius: 14px;
  width: 100%;
  background-color: var(--bg-def-dark2);
  padding: 13px 34px;
}
header .header-wrapper .wrap {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header-bottom__item h3 {
  color: white;
  font-size: 20px;
  font-weight: 500;
  line-height: 26px;
  margin: 10px 0;
}

.header-bottom__links {
  display: flex;
  align-items: center;
  column-gap: 32px;
  list-style: none;
  padding-bottom: 10px;
}
.header-bottom__links li a {
  display: flex;
  align-items: center;
  column-gap: 10px;
  color: var(--bg-F8F8F8);
  font-size: 18px;
  font-weight: 500;
  line-height: 24px;
}

header.open {
  z-index: 4;
}
header.open .header-wrapper {
  box-shadow: 0px 3px 4px 0px rgba(0, 0, 0, 0.14);
}
header.open .header-dropdown img {
  transform: rotate(-180deg);
}

.header-bottom {
  overflow: hidden;
  height: 0;
  opacity: 0;
}

.nav-links {
  display: flex;
  align-items: center;
  list-style: none;
  column-gap: 35px;
}
.nav-links li a {
  display: flex;
  align-items: center;
  column-gap: 6px;
  color: #F8F8F8;
  leading-trim: both;
  text-edge: cap;
  font-size: 17px;
  font-weight: 500;
  line-height: 26px;
}

.navbar-btns {
  display: flex;
  align-items: center;
  column-gap: 10px;
}

.lang-dropdown {
  position: relative;
}
.lang-dropdown .dropdown-menu {
  display: none;
  opacity: 0;
  position: absolute;
  z-index: 3;
  border-radius: 8px;
  background: #333;
  box-shadow: 0px 0px 6px 0px rgba(0, 0, 0, 0.21);
}
.lang-dropdown .dropdown-menu li {
  list-style: none;
  padding: 5px 16px;
}
.lang-dropdown .dropdown-menu.show {
  display: block;
  opacity: 1;
}

.dropdown-menu {
  width: 177px;
  padding: 10px 0;
  top: calc(100% + 8px);
  right: 0;
}
.dropdown-menu li {
  width: 100%;
}
.dropdown-menu li a {
  display: flex;
  align-items: center;
  column-gap: 3px;
  color: var(--bg-F8F8F8);
  font-size: 16px;
  font-weight: 500;
  line-height: 100%;
}
.dropdown-menu li:hover {
  background-color: #4d4d4d;
}

.lang-btn {
  background-color: var(--bg-343434);
  color: var(--bg-F8F8F8);
  text-align: center;
  leading-trim: both;
  text-edge: cap;
  font-size: 17px;
  font-weight: 500;
  text-transform: uppercase;
}

.navbar-btns .login-btn,
.navbar-btns .lang-btn,
.navbar-btns .dark-light-btn {
  height: 36px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 15px;
}
.navbar-btns .dark-light-btn {
  width: 36px;
  height: 36px;
  padding: 6px;
}
.navbar-btns .lang-btn {
  width: 53px;
  overflow: hidden;
}

.navbar-logo {
  display: block;
  overflow: hidden;
  height: 50px;
  display: flex;
  align-items: center;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}
.navbar-logo img {
  width: 140px;
