}

.accordion {
  width: 100%;
  border-radius: 8px;
  background: var(--bg-def-dark2);
}
.accordion .accordion-head {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 15px;
  user-select: none;
  cursor: pointer;
}
.accordion .accordion-head h3 {
  color: var(--bg-F8F8F8);
  font-size: 18px;
  font-weight: 500;
  line-height: 24px;
}
.accordion .accordion-content {
  overflow: hidden;
  height: 0;
}
.accordion .accordion-content p {
  color: #999;
  padding: 15px;
  padding-top: 5px;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
}

.accordion.active .accordion-content {
  height: auto;
}

.faq-card {
  padding: 30px;
  background-color: var(--bg-def-dark2);
  border-radius: 20px;
  height: 100%;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  flex-direction: column;
  max-height: 200px;
}
.faq-card h3 {
  color: var(--bg-F8F8F8);
  font-size: 22px;
  font-weight: 500;
  line-height: 24px;
  margin-bottom: 10px;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
.faq-card p {
  color: #999;
  font-size: 16px;
  font-weight: 500;
  line-height: 18px;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
.faq-card a {
  display: block;
  color: #23C1FF;
  font-size: 16px;
  font-weight: 600;
  line-height: 18px;
  margin-top: 20px;
}

/*======================
	Privacy
======================*/
#privacy {
  margin-top: 22px;
}
#privacy .privacy-wrapper {
  padding: 40px 34px;
  border-radius: 14px;
  background-color: #191919;
  display: flex;
  align-items: flex-start;
  column-gap: 90px;
}

.privacy-left {
  width: 100%;
  max-width: 335px;
  flex-shrink: 0;
}

.privacy__title {
  margin-bottom: 25px;
  color: var(--bg-F8F8F8);
  font-size: 20px;
  font-weight: 600;
  line-height: 22px;
}

.left-sieries {
  list-style: none;
  color: white;
}
.left-sieries li {
  color: var(--bg-F8F8F8);
  font-size: 18px;
  font-weight: 500;
  line-height: 100%;
  padding: 15px;
  border-radius: 5px;
  user-select: none;
  cursor: pointer;
}
.left-sieries li > b {
  font-weight: 600;
  line-height: 22px;
}
.left-sieries li.active {
  background-color: #333;
}
.left-sieries li span {
  display: -webkit-box;
  width: 100%;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.privacy-right {
  width: 100%;
}
.privacy-right .privacy__title {
  font-size: 24px;
  margin-bottom: 50px;
}

.right-series__item p,
.right-series__item b {
  color: var(--bg-F8F8F8);
  font-size: 18px;
  font-weight: 500;
  line-height: 22px;
  display: block;
}
.right-series__item b {
  font-weight: 600;
}
.right-series__item:not(:last-child) {
  margin-bottom: 25px;
}

.right-sieries__content {
  margin-top: 10px;
  height: 0;
  overflow: hidden;
  transition: ease-in 0.3s;
}
.right-sieries__content p {
  padding: 18px;
  border-radius: 8px;
  background: #313131;
  color: #C9C9C9;
  font-size: 16px;
  font-weight: 500;
  line-height: 22px;
}

.right-sieries__content.active {
  height: auto;
}

/*======================
	Footer
=====================*/
footer {
  margin: 66px 0;
}
footer .footer-wrap {
  background-color: var(--bg-def-dark2);
  border-radius: 20px;
  padding: 30px 40px;
}

.footer-head {
  padding-bottom: 20px;
  margin-bottom: 35px;
  border-bottom: 1px solid #4D4D4D;
}
.footer-head .footer-logo {
  display: flex;
  align-items: center;
  margin-left: -37px;
}
.footer-head img {
  display: block;
}
.footer-head img:first-child {
  width: 140px;
}
.footer-head img:last-child {
  margin-left: -20px;
}

.footer-series {
  display: flex;
  align-items: flex-start;
  column-gap: 90px;
}
.footer-series .footer-series__item {
  list-style: none;
}

.footer-series__item li {
  color: #8A8A8A;
  font-size: 18px;
  font-weight: 500;
  line-height: 18px;
}
.footer-series__item li a {
  color: #F8F8F8;
  font-size: 16px;
  font-weight: 500;
  line-height: 18px;
}
.footer-series__item li:not(:last-child) {
  margin-bottom: 15px;
}

.footer-bottom {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  flex-wrap: wrap;
  column-gap: 10px;
  border-top: 1px solid #4D4D4D;
  padding-top: 20px;
  margin-top: 60px;
}
.footer-bottom .footer-bottom__item {
  width: 100%;
  display: flex;
  align-items: center;
  column-gap: 15px;
}
.footer-bottom .footer-bottom__item p {
  color: #8A8A8A;
  font-size: 14px;
  font-weight: 500;
  line-height: 18px;
}
.footer-bottom .footer-bottom__item:first-child {
  max-width: 300px;
}
.footer-bottom .footer-bottom__item:nth-child(2) {
  width: fit-content;
  flex-shrink: 0;
}
.footer-bottom .footer-bottom__item:nth-child(2) p {
  flex-shrink: 0;
}
.footer-bottom .footer-bottom__item:nth-child(3) {
  text-align: end;
  max-width: 350px;
}

